export default {
  state: {
    attention: 0,
    shareModal: false
  },
  mutations: {
    SET_ATTENTION(state, att){
      state.attention = att
    },
    SET_SHARE(state, modal){
    	state.shareModal = modal
    }
  },
  actions: {
    set_attention(state, att){
      state.commit('SET_ATTENTION', att)
    },
    set_share(state, modal){
    	state.commit('SET_SHARE', modal)
    }
  },
  getters: {
    get_attention(state){
      return state.attention
    },
    get_share(state){
    	return state.shareModal
    }
  }
}



